.Testimonials{
    display: flex;
    gap: 2rem;
    padding: 0 2rem;
}

.left-t{
    display: flex;
    flex-direction: column;
    flex: 1 1;
    gap: 2rem;
    text-transform: uppercase;
    color: white;
}
.left-t>:nth-child(1){
    color: var(--orange);
    font-weight: bold;

}
.left-t>:nth-child(2),
.left-t>:nth-child(3)
{
   font-size: 3rem;
    font-weight: bold;
    
}
.left-t>:nth-child(4)
{
    text-transform: none;
    text-align: justify;
    letter-spacing: 2px;
    line-height: 40px;
}
.right-t
{
    flex: 1;
    position: relative;
}
.right-t>img{
    position: absolute;
    height: 20rem;
    width: 17rem;
    object-fit: cover;
    right: 8rem;
    top: 2rem;
}
.right-t>:nth-child(1){
    position: absolute;
    width: 17rem;
    height: 20rem;
    border: 2px solid orange;
    background-color: transparent;
    right: 9rem ;
    top: 0.9rem;
}
.right-t>:nth-child(2){
    position: absolute;
    width: 17rem;
    height: 20rem;
    /* border: 2px solid orange; */
    background: var(--planCard);
    right: 7rem ;
    top: 3rem;
}
.arrows{
    display: flex;
    position: absolute;
    bottom: 1rem;
    left: 3rem;
    gap: 1rem;
    
}
.arrows>img{
    width: 1.5rem;
    cursor: pointer;
}
@media screen and (max-width :768px){
    .Testimonials{
        flex-direction: column;
    }
    .left-t>:nth-child(2),
    .left-t>:nth-child(3)
    {
   font-size: xx-large;   
   }
   .right-t{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    gap: 2rem;
   }
   .right-t>div{
    position: relative;
    display: none;
   }
   .right-t>img{
    top: 0;
    right: 0;
    position: relative;
    align-self: center;
   }
   .right-t>:last-child{
    display: block;
    bottom: 0;left: 0;
   }
    
}